<template>
  <b-table
    ref="productTable"
    responsive="sm"
    :items="items"
    :fields="fields"
    :small="true"
    @row-clicked="productClicked"
    @row-dblclicked="productDoubleClicked"
  >
    <template v-slot:cell(name)="data">
      <div class="row">
        <div class="col-4" style="overflow: hidden">
          <img :src="data.item.thumbnail" width="150px" height="150px" style="margin-left: -30px" />
        </div>
        <div class="col-8">
          <strong>
            <text-highlight :queries="queries">{{data.item.name}}</text-highlight>
          </strong>
          <br />
          <strong>Weight:</strong>
          {{data.item.weight}}(g)
          - {{data.item.shipPrice | currency(audConfig)}}
          <br />
          <strong>CW Price</strong>
          : {{data.item.cwPrice | currency(audConfig)}} -
          <strong>Sale</strong>
          : {{data.item.cwSalePrice | currency(audConfig)}}
          <br />
          <strong style="width: 100px;">BV Price</strong>
          : {{data.item.bvPrice | currency(audConfig)}} -
          <strong>Sale</strong>
          : {{data.item.bvSalePrice | currency(audConfig)}}
          <br />

          <span class="h4">
            <strong>Price</strong>
            : {{data.item.rndPrice | currency(vndConfig)}}
          </span>
          <b-badge v-if="data.item.bvSalePricePick" variant="success">BV Sale</b-badge>
          <b-badge v-if="data.item.cwSalePricePick" variant="success">CW Sale</b-badge>

          <div style="margin-top: 7px">
            <b-button
              type="submit"
              size="sm"
              variant="success"
              @click="editProduct(data)"
              class="mr-1"
            >
              <i class="fa fa-edit"></i> Edit
            </b-button>
            <b-button type="submit" size="sm" variant="danger" @click="deleteProduct(data)">
              <i class="fa fa-remove"></i> Delete
            </b-button>
          </div>
        </div>
      </div>
    </template>
  </b-table>
</template>

<script>
import productList from "./productsList";

export default {
  name: "ProductsList",
  components: {},
  props: [],
  extends: productList,
  data() {
    return {
      fields: [{ key: "name", label: "", class: "" }]
    };
  },
  mounted: function() {},
  computed: {},
  methods: {}
};
</script>
<style>
.label {
  font-weight: bold;
}

.name {
  width: 20%;
}
.weight {
  width: 10%;
}
.temp-price {
  width: 40%;
}
.rnd-price {
  width: 20%;
}
.rnd-price .price {
  font-size: 18px;
  font-weight: bold;
  margin-right: 5px;
}

.action {
  width: 10%;
}
</style>
